.ports{
	width: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	max-height: 100vh;
	background: #111;
}
.contain{
	display: flex;
	justify-content: center;
	align-items: center;
	flex-wrap: wrap;
	max-width: 1200px;
	margin: 40px 0;
}
.contain .cardu{
	position: relative;
	min-width: 320px;
	height: 440px;
	box-shadow: inset 5px 5px 5px rgba(0,0,0,0.2),
				inset -5px -5px 15px rgba(255,255,255,0.1),
				5px 5px 15px rgba(0,0,0,0.3),
				-5px -5px 15px rgba(255,255,255,0.1);
	border-radius: 15px;
	margin: 30px;
}
.contain .cardu .boxu{
	position: absolute;
	top: 20px;
	left: 20px;
	right: 20px;
	bottom: 20px;
	background: #2a2b2f;
	box-shadow: 0 20px 50px rgba(0,0,0,0.5);
	border-radius: 7px;
	transition: 0.5s;
	display: flex;
	justify-content: center;
	align-items: center;
	overflow: hidden;
}
.contain .cardu .boxu::before{
	content: '';
	position: absolute;
	top: 0;
	left: 0;
	width: 50%;
	height: 100%;
	background: rgba(255,255,255,0.05);
}
.contain .cardu .boxu:hover{
	transform: translateY(-50px);
	box-shadow: 0 40px 70px rgba(0,0,0,0.5);
}
.contain .cardu .boxu .content{
	padding: 20px;
	text-align: center;
}
.contain .cardu .boxu .content h2{
	position: absolute;
	top: -5px;
	right: 30px;
	font-size: 8em;
	color: rgba(255,255,255,0.05);
}
.contain .cardu .boxu .content h3{
	font-size: 1.8em;
	color: rgba(255, 255, 255, 0.925);
	z-index: 1;
	transition: 0.5s;
}
.contain .cardu .boxu .content p{
	font-size: 1em;
	color: rgba(255, 255, 255, 0.925);
	z-index: 1;
	transition: 0.5s;
}
.contain .cardu .boxu .content a{
	position: relative;
	display: inline-block;
	padding: 8px 20px;
	background: #000;
	margin-top: 15px;
	border-radius: 20px;
	text-decoration: none;
	font-weight: 400;
	color: #fff;
	box-shadow: 0 10px 20px rgba(0,0,0,0.2);
}
.contain .cardu:nth-child(1) .boxu .content a{
	background: #2196f3;
}
.contain .cardu:nth-child(2) .boxu .content a{
	background: #e91e63;
}
.cardu .boxu .content img{
	height: 100px;
	width: 200px;
	margin: 10px 20px;
	border-radius: 10px;
}
.contain .cardu .boxu .content a:hover{
	background: #e91e63;
	transition: 0.4s;
}
